    line-height: 42px;
    padding-bottom: 18px;
}
.icon-box h4{
    font-size: 24px;
}
.icon-box p{
    font-size: 14px;
    margin-bottom: 0;
}
.icon-box-link{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 15px;
    text-align: right;
}
.icon-box-link a{
    font-size: 20px;
    line-height: 40px;
    width: 40px;
    text-align: center;
    display: inline-block;
    border-radius: 40px;
    background-color: rgba(255, 255, 255, 0.4);
    transform: rotate(-45deg);
    transition: all 0.4s ease;
}
.icon-box-link a:hover{
    background-color: rgba(255, 255, 255, 0.5);
    transform: rotate(0deg);
}
.timeline-list ul{
    margin: 0;
    padding: 0;
    list-style-type: none;
}
.timeline-list li{
    position: relative;
    margin-bottom: 10px;
    padding-left: 48px;
}
.timeline-list li:before{
    content: '';
    position: absolute;
    left: 15px;
    top: 50%;
    width: 1px;
    height: calc(100% + 10px);
    background-color: #b1b1b1;
}
.timeline-list li:last-child{
    margin-bottom: 0;
}
.timeline-list li:last-child:before{
    display: none;
}
.timeline-list li .fa{
    line-height: 30px;
    color: #fff;
    width: 30px;
    display: inline-block;
    text-align: center;
    border-radius: 30px;
    background-color: #2d62ec;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(0%, -50%);
    z-index: 1;
}
.timeline-list li p{
    margin-bottom: 0;
}
.timeline-list li.completed .fa{
    background-color: #2ecd79;
}
.timeline-list li.pending .fa{
    background-color: #2e61ec;
    background: linear-gradient(0deg, #2e61ec 0%, #483680 100%);
}
.sub-menu{
    background-color: #fff;
    border-radius: 15px;
    margin-top: 5px;
    opacity: 0;
    visibility: hidden;
    margin: 0;
    padding: 0;
    list-style-type: none;
    position: absolute;
    top: 100%;
    right: -15px;
    min-width: 200px;
    box-shadow: 2px 2px 6px 0px rgba(0, 0, 0, 0.1);
    transition: all 0.2s ease;
}
.sub-menu:hover{
    margin-top: -1px;
    opacity: 1;
    visibility: visible;
    transition: all 0.4s ease;
}
.sub-menu li{
    font-size: 14px;
    line-height: 18px;
    color: #212529;
    font-weight: 600;
    border-bottom: 1px solid #e4e4e4;
}
.sub-menu li:last-child{
    border-bottom: 0;
}
.sub-menu li a{
    display: block;
    padding: 10px 15px;
    color: #212529;
    text-decoration: none;
}
